  width: 200px;
  background-color: #ecf0f0;
  border-color: rgba(0, 0, 0, 0);
  margin-bottom: 70px;
  font-size: 20px;
}

.breed-select:focus, .breed-select:hover {
  border: 2px solid #e08080;
  outline: none;
}

.cat-image {
  width: 50%;
  height: 50%;
}

.cat-info {
  gap: 20px;
  display: flex;
}

.cat-text {
  max-width: 500px;
  font-size: 20px;
}

.title {
  color: #0e183b;
  margin-right: 5px;
  font-weight: 700;
}

.hidden {
  display: none;
}

.loader {
  letter-spacing: 6px;
  font-size: 30px;
  position: relative;
  top: 150px;
}

.loader:before {
  content: "Loading";
  color: #fff;
}

.loader:after {
  content: "";
  width: 20px;
  height: 20px;
  transform-origin: bottom;
  background-color: #ff3d00;
  background-image: radial-gradient(2px, rgba(255, 255, 255, .267) 100%, rgba(0, 0, 0, 0) 0), radial-gradient(1px, rgba(255, 255, 255, .2) 100%, rgba(0, 0, 0, 0) 0);
  background-position: 14px -4px, 12px -1px;
  border-radius: 50%;
  margin: auto;
  animation: fillBaloon 1s ease-in-out infinite alternate;
  position: absolute;
  top: -10px;
  right: 54px;
}

@keyframes fillBaloon {
  0% {
    transform: scale(1);
  }

  100% {
    transform: scale(3);
  }
}

